
.main-component {
  padding: 1rem;
}
.main-component .step-header .sheet-not-normalized {
    color: #dd4b39;
    text-align: center;
    font-size: 1.4rem;
    margin-left: 2rem;
}
.main-component .step-container section {
    margin-top: 2rem;
}
.main-component .step-container section .header > div {
      margin-left: 1rem;
}
.main-component .step-container section .header > div > label {
        margin-right: 4.5rem;
        font-weight: bold;
        font-size: 0.91rem;
}
.main-component .step-container section .header > div > input {
        width: 1rem;
        height: 1rem;
        text-align: center;
}
.main-component .step-container section .container {
      margin-left: 1rem;
}
.main-component .step-container section .container > div {
        margin-top: 0.5rem;
        margin-bottom: 0.5rem;
}
.main-component .step-footer {
    text-align: right;
    padding: 5px;
}
.inline-group {
  display: table;
  width: 25rem;
}
.inline-group > label,
  .inline-group > input,
  .inline-group > select {
    display: table-cell;
}
.inline-group > label {
    width: 12rem;
}
.active-error > .error {
  display: block;
  margin-left: 1rem;
  padding-top: 0.5rem;
}
.active-error input,
.active-error select {
  border-style: solid;
  border-color: #dd4b4c;
}
.error {
  display: none;
}
.code {
  display: block;
  background: #e2e0e4;
  font-size: 0.3rem;
  text-align: justify;
}
.code pre {
    white-space: -moz-pre-wrap;
    /* Mozilla, supported since 1999 */
    white-space: -pre-wrap;
    /* Opera */
    white-space: -o-pre-wrap;
    /* Opera */
    white-space: pre-wrap;
    /* CSS3 - Text module (Candidate Recommendation) http://www.w3.org/TR/css3-text/#white-space */
    word-wrap: break-word;
    /* IE 5.5+ */
}



