
.custom-item-component {
  display: table;
  margin-top: 0.5rem;
}
.custom-item-component .custom-input-title,
  .custom-item-component .custom-input-value,
  .custom-item-component .custom-item-delete {
    display: table-cell;
}
.custom-item-component .custom-input-value {
    width: 2rem;
}
.custom-item-component .custom-input-value > input {
      min-width: 8.5rem;
      height: 2.0rem;
}
.custom-item-component .custom-input-title {
    width: 12rem;
}
.custom-item-component .custom-input-title > input {
      min-width: 10.5rem;
      height: 2.0rem;
}
.custom-item-component .custom-item-delete span {
    margin-left: 0.5rem;
    text-transform: uppercase;
    background-color: #d64937;
    color: white;
    border-color: #d64937;
    border-style: solid;
    width: 1rem;
    text-align: center;
}



